<template>
	<div class="app-wrap">
		<div class="app-header">
			<div class="app-header-inner">
				<router-link :to="{path: '/'}">
					<img src="../assets/logo.png">
				</router-link>
					<div class="head-nav">
						<ul class="nav-list">
							<li @click="regClick"></li>
							<li class="nav-pile"></li>
							<li @click="logClick"></li>
							<li class="nav-pile"></li>
							<li @click="aboutClick"></li>				<!-- aboutmethods -->
						</ul>
					</div>
				</div>
		</div>
			
		<div class="app-content">
			<keep-alive>						<!--  -->
				<router-view></router-view>
			</keep-alive>
		</div>
		<div class="app-footer">
			<p>jiangheng</p>
		</div>
		<my-dialog :is-show="isShowAboutDialog" @on-close="closeDialog('isShowAboutDialog')">			<!-- isshowdialog -->
			<p> </p>												<!-- on-closeclosed -->
		</my-dialog>

		<my-dialog :is-show="isShowLogDialog" @on-close="closeDialog('isShowLogDialog')">			
					<reg-form>  </reg-form>							
		</my-dialog>

		<my-dialog :is-show="isShowRegDialog" @on-close="closeDialog('isShowRegDialog')">	
					<log-form>	</log-form>			
											
		</my-dialog>
	</div>
</template>
<script>
import Dialog from './dialog'
import LogForm from './LogForm'
import RegForm from './RegForm'
	export default{
		components: {
			MyDialog: Dialog,
			LogForm,
			RegForm
		},
		data () {
			return{
				isShowAboutDialog: false,
				isShowLogDialog: false,
				isShowRegDialog: false,
			}
		},
		methods: {
			aboutClick(){
				this.isShowAboutDialog = true
			},
			logClick(){
				this.isShowLogDialog = true
			},
			regClick(){
				this.isShowRegDialog = true
			},
			closeDialog(attr){
				this[attr]= false
			}
		}
	}
</script>
<style>
	*{
		margin: 0;
		padding: 0

	}
	.aoo-wrap{
		width: 100%;
		height: 100%
	}
	.app-header{
		width: 100%;
		height: 90px;
		background-color: #454545;
	}
	img{
		width: 80px;
		margin-top: 0.5%; 
		margin-left: 8%; 
	}
	ul{
		position: relative;
		left: -200px;
		top: -58px;
		color: #fff;
		float: right;
	}
	li{
		margin: 10px;
		display: inline;
		font-size: 20px;	
	}
	li:hover{
		cursor:pointer
	}
	.nav-pile{
		width: 30px;
		height: 25px;
	}
	.app-footer{
		width: 100%;
		text-align: center;
		position: absolute;
		top:1030px;
		background-color: #d4d4d4
	}
	.app-content{
		background-color: #e8e8e8;
		height: 950px
	}
</style>


